body{
    /*    background-color: #e9ebee;*/
        color: #1d2129;
        font-size:14px;
        padding-top:60px; 
        font-family:Arial, Helvetica, sans-serif;
        min-width: 1360px;
    }
    
    header{
        background-color:#4267B2;
        color: white;
        position:fixed;
        top:0px;
        z-index:200;
        left:0px;
        width: 100%;
    }
    .no-mrg{
        margin: 0px;
    }
    header ul>li>a,header ul>li>a:hover{
        color: white;
        text-decoration: none;
        padding:5px;
        margin: 8px 0px 8px;
        font-size:16px;
        border-radius: 2px;
    }
    header ul>li:hover{
        background-color: #3B5DA0;
    }
    .navbar-brand{
        color: white;
    }
    header ul{
        padding: 0px;
        margin: 0px;
        list-style-type: none;
    }
    header ul>li{
        padding:14px 5px;
        display: inline-block;
    }
    header .search_input.pull-left{
        width:50%; 
    }
    header input{
        width:calc(100% - 65px);
        padding:5px;
        margin:7px 0px;
        border-radius: 2px 0px 0px 2px; 
        border:none;
        margin-left: 15px;
    }
    header #search{
        color: #1d2129;
        padding:5px 15px 5px;
        margin:7px 0px;
        border: none;
        font-size: 14px;
        border-radius: 0px 2px 2px 0px; 
        background-color:#f6f7f9;
    }
    .bg-white,.bg-white-hover:hover{
        background-color: white;    
        border: 1px solid #dddfe2;
        border-radius:3px;
        padding:12px;
    }
    .thumbnail{
        margin-bottom: 10px;
        position: relative;
    }
    .scrollbox{
        height:calc(100vh - 120px);
        overflow-y: auto;
        padding:8px 5px 0px 10px;
    }
    
    .img-overlay,.video-overlay{
        position: absolute;
        width: 100%;
        height: 100%;
        background-color:rgba(255,255,255,0.1);
        display:-ms-flexbox;
        display:flex;
        z-index:10;
        top:0px;
        left:0px;
        -ms-flex-pack:center;
            justify-content:center;
        cursor:pointer;
    }
    .img-overlay{
        display: none;
    }
    .images-box .thumbnail:hover .img-overlay{
        background:rgba(0,0,0,0.2);
        display: -ms-flexbox;
        display: flex;
        color: white;
    }
    .images-box .thumbnail:hover .img-overlay .fa{
        background:rgba(255,255,255,1);
        color:#1d2129;
        font-size:26px;
        padding:5px; 
    }
    .images-box .thumbnail.selected .img-overlay,.videos-box .thumbnail.selected .video-overlay{
         background:rgba(0,0,0,0.1);
    background: -webkit-linear-gradient(top,  rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 37%,rgba(0,0,0,0) 100%); 
    background: -webkit-gradient(linear,  left top, left bottom,  from(rgba(0,0,0,0.65)),color-stop(37%, rgba(0,0,0,0)),to(rgba(0,0,0,0))); 
    background: -o-linear-gradient(top,  rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 37%,rgba(0,0,0,0) 100%); 
    background: linear-gradient(to bottom,  rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 37%,rgba(0,0,0,0) 100%); 
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=0 );
        -ms-flex-pack:start;
            justify-content:flex-start;
        display:-ms-flexbox;
        display:flex;
    }
    .images-box .thumbnail.selected .img-overlay .fa{
         background:rgba(0,105, 255,0.6);
         color:white; 
         padding: 5px;
        -ms-flex-item-align:start;
            align-self:flex-start;
        margin: 5px;
    }
    .img-overlay .fa,.video-overlay .fa,.videos-box .thumbnail.selected:hover .video-overlay .fa{
        -ms-flex-item-align:center;
            align-self:center;
        font-size:26px;
        color:rgba(0,105, 255,0.8);
        border-radius:50px;
    }
    
    .video-overlay .fa{
         background-color:rgba(255,255,255,0.45);
         padding:10px 10px 10px 15px;  
    }
    .videos-box .thumbnail:hover .video-overlay .fa{
        background: white;
    }
    
    .videos-box .thumbnail.selected .video-overlay .fa,.videos-box .thumbnail.selected:hover .video-overlay .fa{
        color:rgba(0,105, 255,0.6);
         background:white; 
         padding: 5px;
        -ms-flex-item-align:start;
            align-self:flex-start;
        margin: 5px;
   
    }
    .hide{
        display: none;
    }
    canvas{
        background: #eee;
        /* width: calc(100% - 60px); */
        /* height: calc(100vh - 200px); */
    }
    .screen-options{
        width:60px;
        float: right;
    }
    .screen-options .btn{
        display: block;
    }
    .screen-options .nav>li>a{
        padding:10px 4px; 
        color: white;
        background:#0069ff;
        border: 1px solid #ccc;
        border-radius:2px;
    }
    .screen-options .nav>li>a:hover{
        background-color: white;
        color:#0069ff;
    }
    p{
        margin-bottom: 0px;
    }
    .screen-options .fa{
        font-size: 20px;
    }
    .navbar-close{
        font-size:22px;
        /* padding: 10px; */
    }
    .navbar{
        border-right: 1px solid #eee;
    }
    .padd_lr_5{
        padding: 0px 5px;
    }
    .dark{
        font-weight: 700;
    }
    .inline-block{
        display: inline-block;
    }
    a:hover{
        cursor: pointer;
    }
    .face-object-box{
        position:absolute;
        z-index: 100;
    }
    .face-box{
        border:2px solid black;
        background:rgba(0,0,0,0.3); 
        resize: both;
    }
    .canvas-container{
        /* position: relative; */
    }
    .face-object-box-container{
        position:absolute;
        z-index:50;  
        width:100%;
        height: 100%;
        border:1px solid #ccc;
        top:0px;
        left:0px; 
    }
    .face-object-box-rel-container{
        position: relative;
        display:-ms-flexbox;
        display:flex;
        -ms-flex:1 1;
            flex:1 1;
    }
    .face-info input,.face-info input{
        display:block;
        padding:2px;
        border:none;
        max-width:150px;
    }


/****************************/
    .player{
        margin:5px 0px; 
        background: black;
        color:white;
    }

    .video_player a{
        display:inline-block;
        padding:12px 22px;
        font-size:24px; 
        border-right: 1px solid #ccc; 
    }
    #playorpause{
        font-size:28px;
        padding:9px 18px;   
    }
    .video_player a:hover{
        background: #eee;
    }
    .timing-info span{
        display:inline-block;
        padding-top: 5px;
        font-size:16px;
    }
    #current_time{
        padding-right: 5px;
    }
    #end_time{
        padding-left: 5px;
    }
    .player_video_details{
        font-size: 13px;
        border-bottom: 1px solid #ccc;
        padding-top:5px;
    }
    .video_progress{
        margin: 0px 20px 0px 50px;
    }
    #volume_manager {
        background-color: #000; 
        padding: 5px 20px;
        position: absolute;
        text-align: center;
        z-index: 200;
        bottom:55px;
        left:10px;
        display: none;
        border-radius: 5px;
        height:150px;
        -webkit-box-shadow: 0px 0px 20px 0px #eee;
                box-shadow: 0px 0px 20px 0px #eee;
    }
    #volume_slider {
        -webkit-appearance: slider-vertical;
        -webkit-writing-mode: bt-lr;
            -ms-writing-mode: bt-lr;
                writing-mode: bt-lr;
    }
    input[type="range"] {
        -webkit-appearance: none;
        width: 7px;
        background-color: transparent;
    }
    #volume_box{
        position: relative;
    }
    #volume_box:hover #volume_manager{
        display: block;
    }

    /*****************/
    .form input{
        margin-bottom: 2px;
    }
    .row-select{
        background:#FFFFCC;
    }

    


